.mobile-menu {
  width: 250px;
  min-height: 100vh;
  z-index: 300;
  opacity: .3;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 400ms var(--primary-cubic-bezier), visibility 400ms var(--primary-cubic-bezier), transform 500ms var(--primary-cubic-bezier);
  background-color: #7c7c7c;
  padding-top: 50px;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  z-index: 300;
  transform: translateX(0);
}

.mobile-menu__list {
  width: 100%;
