  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
  text-decoration: none;
  display: block;
}

.mobile-menu__link.mobile-menu__link--current, .mobile-menu__link:hover, .mobile-menu__link:focus {
  color: #6cbbe9;
}

.close__btn {
  width: 25px;
  height: 25px;
  transition: background-color 250ms var(--primary-cubic-bezier), transform 250ms var(--primary-cubic-bezier);
  border-radius: 50%;
  position: absolute;
  top: 15px;
  right: 15px;
  transform: rotate(90deg);
}

.close__btn:hover, .close__btn:focus {
  background-color: #6cbbe9;
  transform: rotate(-90deg);
}

.open__btn--svg {
  stroke: #000;
  fill: #fff;
}

.close__btn--svg {
  width: 14px;
  height: 14px;
  stroke: var(--text-color-theme-dark);
  stroke-width: 4px;
  transition: stroke 250ms var(--primary-cubic-bezier);
}

.close__btn:hover .close__btn--svg, .close__btn:focus .close__btn--svg {
  stroke: #000;
}

